@import url(https://fonts.googleapis.com/css2?family=Aclonica&display=swap);
.card {
  background: #fff;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 3px 2px 6px rgb(54, 54, 54);
  text-align: left;
  cursor: pointer;
  border: none;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: black;
}

.card > .img-container > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.wrapper {
  background: #3f6b36;
  background-image: url("https://cdn.pixabay.com/photo/2018/10/12/21/09/meadow-3743023_1280.jpg");
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: rgb(217, 247, 133);
  font-family: 'Aclonica', sans-serif;
}


