* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.navbar {
    width: 100%;
    margin-bottom: 30px;
    background-color: rgb(18, 24, 18); 
}

.navbar-text {
    width: 100%;
    text-align: center;
    padding: 5px;
    margin: 0;
    color: white;
    word-spacing: .2em;
}


